<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.auther}}</p>
        <p>分类：</p>
        <ul>
            <li v-for="item in blog.arrtext" :key="item.id">{{item}}</li>
        </ul>
        <button v-on:click="deleterblog()">删除</button>
        <router-link v-bind:to="'/edit/'+id">编辑</router-link>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name:'single-blog',
    data(){
        return{
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
        // this.$http.get("https://myblogs-80396.firebaseio.com/posts/"+this.id+".json")
        // .then(function(data){
        //     // this.blog=data.body;
        //     // console.log(data);
        //     return data.body;
        // })
        // .then(function(data){
        //     this.blog=data;
        // })
        axios.get("https://myblogs-80396.firebaseio.com/posts/"+this.id+".json")
        .then(data =>{
            this.blog=data.data;
        })
    },
    methods:{
        deleterblog(){
            // this.$http.delete("https://myblogs-80396.firebaseio.com/posts/"+this.id+".json")
            axios.delete("https://myblogs-80396.firebaseio.com/posts/"+this.id+".json")
            .then(response =>{
                this.$router.push("/");
            })
        }
    }
}
</script>

<style scoped>

#single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
}
</style>


