<template>
    <div id="show-blogs" v-theme="'lg'">
        <h2>博 客</h2>
        <input type="text" v-model="search" placeholder="placeholder">
        <div class="single-blog" v-for="blog in filteredBlogs" :key="blog.id">
            <router-link v-bind:to="'/blog/'+blog.id">
            <h2 v-div>{{blog.title | to-uppercase}}</h2>
            </router-link>
            <article>
                {{blog.content | content}}
            </article>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:'show-blogs',
    data(){
        return{
            blogs: [],
            search:""
        }
    },
    created(){
        // this.$http.get("https://myblogs-80396.firebaseio.com/posts.json")
        // .then(function(data){
        //     console.log(data.json()); 
        //     // this.blogs=data.body.slice(0,10);
        //     return data.json();

        // }).then(function(data){
        //     var blogsarray=[];
        //     for(let key in data){
        //         // console.log(key);
        //         // console.log(data[key]);
        //         data[key].id=key;
        //         blogsarray.push(data[key]);
        //     }
        //     // console.log(blogsarray);
        //     this.blogs = blogsarray;
        // })

        // 使用axios get请求
        axios.get("https://myblogs-80396.firebaseio.com/posts.json").then(data =>{
            console.log(data)
            let blogsarray= [];
            for(let key in data.data){
                data.data[key].id=key;
                blogsarray.push(data.data[key]);
            }
            this.blogs = blogsarray;
        })
    },
    computed:{
        filteredBlogs:function(){
            return this.blogs.filter((blogs)=>{
                return blogs.title.match(this.search);
            })
        }
    },
    filters:{
        'content':function(value){
            return value.slice(0,100)+"...";
        }
    }
}
</script>

<style>
#show-blogs{
    max-width: 800px;
    margin: 0 auto;

}
.single-blog{
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
}
#show-blogs a{
    color: #444;
    text-decoration: none;
}
input[type="text"]{
    padding: 8px;
    width:100%;
    box-sizing: border-box;
}
</style>

