<template>
    <div id="add-blog">
        <h2 class="title">编辑博客</h2>
        <form action="" v-if="!submmited">
            <label for="">博客标题</label>
            <input type="text" v-model="blog.title" required>

            <label for="">博客内容</label>
            <textarea name="" id="" cols="30" rows="10" v-model="blog.content"></textarea>

            <div id="checkboxes">
                <label for="">vue.js
                    <input type="checkbox" value="vue.js" v-model="blog.arrtext">
                </label>
                
                <label for="">node.js
                    <input type="checkbox" value="node.js" v-model="blog.arrtext">
                </label>
                
                <label for="">react.js
                    <input type="checkbox" value="react.js" v-model="blog.arrtext">
                </label>
                
                <label for="">angular4.js
                    <input type="checkbox" value="angular4.js" v-model="blog.arrtext">
                </label>
                
            </div>
            <label for="">作者：</label>
            <select  v-model="blog.author">
                <option v-for="auth in authors">{{auth}} </option>
            </select>
            <button v-on:click.prevent="post">编辑博客</button>
        </form>

        <div v-if="submmited">
            <h3 class="title">博客修改成功</h3>
        </div>
        <hr>
        <div id="preview">
            <h3>博客总览</h3>
            <p>博客标题:{{blog.title}}</p>
            <p>博客内容:</p>
            <p>{{blog.content}}</p>
            <p>博客分类:</p>
            <ul>
                <li v-for="(item,key) in blog.arrtext " >
                    {{item}}
                </li>
            </ul>
            <p>作者：{{blog.author}}</p>
        </div>
    </div>
</template>
<script>
//http://jsonplaceholder.typicode.com/
import axios from 'axios'
export default {
    name: 'edit-blog',
    data () {
        return {
            id:this.$route.params.id,
            blog: {
                
            },
            submmited: false,
            authors: ['hemiah','tom','henry'],
        }
    },
    methods: {
        fetchdata:function(){
            // console.log(this.id);
            // this.$http.get("https://myblogs-80396.firebaseio.com/posts/"+this.id+".json")
            // .then(response =>{
            //     // console.log(response.body);
            //     this.blog= response.body;
            // })
            // 使用axios
            axios.get("https://myblogs-80396.firebaseio.com/posts/"+this.id+".json")
            .then(response =>{
                this.blog=response.data;
            })
        },
        post: function () {
            // this.$http.put("https://myblogs-80396.firebaseio.com/posts/"+this.id+".json",
            // // {
            //     // title: this.blog.title,
            //     // // body: this.blog.content,
            //     // // userId: 1
            //     // content:this.blog.content,
            //     // arrtext:this.blog.arrtext,
            //     // author:this.blog.author,
            // // }
            // this.blog
            // ).then(function (data) {
            //     console.log(data);
            //     this.submmited=true;
            // });

            axios.put("https://myblogs-80396.firebaseio.com/posts/"+this.id+".json",this.blog)
            .then(response =>{
                this.submmited=true;
            })
        }
    },
    created(){
        this.fetchdata();
    }
}
</script>
<style scoped>
.title{
    width: 100%;
    text-align: center;
}



#add-blog *{
    box-sizing: border-box;

}
#add-blog {
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
}
label{
    display: block;
    margin: 20px 0 10px;
}
input[type="text"],
textarea,
select{
    display: block;
    width: 100%;
    padding: 8px;
}
textarea{
    height: 200px;
}
#checkboxes label{
    display: inline-block;
    margin-top: 0px;

}
#checkboxes input{
    display: inline-block;
    margin-top: 10px;
}
button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
}
#preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
}
h3{
    margin-top: 10px;
}
label{
    margin-left: 10px;
}
</style>


